import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Details from '@/views/Details.vue'
import ReccomendsForYou from '@/views/RecommendsForYou.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    meta: { layout: 'main', title: '' },
    component: Home
  },
  {
    path: '/new-release',
    name: 'NewRelease',
    meta: { layout: 'main', title: ' ' },
    component: () => import('@/views/NewRelease.vue')
  },
  {
    path: '/film/:id',
    name: 'InfoFilm',
    meta: { layout: 'main', title: '  ' },
    component: Details
  },
  {
    path: '/kinolenta/:name',
    name: 'InfoPlaylist',
    meta: { layout: 'main', title: '' },
    component: Details
  },
  {
    path: '/collection/:colId',
    name: 'InfoCollection',
    meta: { layout: 'main', title: '' },
    component: Details
  },
  {
    path: '/login',
    name: 'Login',
    meta: { layout: 'empty', title: '' },
    component: () => import('../views/Login.vue')
  },
  {
    path: '/registered',
    name: 'Registered',
    meta: { layout: 'empty', title: '' },
    component: () => import('../views/Registered.vue')
  },
  {
    path: '/foryou',
    name: 'RecommendsForYou',
    meta: { layout: 'main', title: '  ' },
    component: ReccomendsForYou
  },
  {
    path: '/search',
    name: 'Search',
    meta: { layout: 'main', title: '' },
    component: () => import('../views/SearchPage.vue')
  },
  {
    path: '/yourMovie',
    name: 'Your Movie',
    meta: { layout: 'main', title: '  ' },
    component: () => import('../views/YourMovie.vue')
  },
  {
    path: '/yourBookmarks',
    name: 'Your Bookmark',
    meta: { layout: 'main', title: ' ' },
    component: () => import('../views/YourBookmark.vue')
  },
  {
    path: '/profile',
    name: 'Profile',
    meta: { layout: 'main', title: '' },
    component: () => import('../views/Profile.vue')
  },

  {
    path: '*',
    name: '404',
    meta: { layout: 'main', title: '  ' },
    component: () => import('../views/404/404.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
